:root {
  --accent: #f2fd38;
  --titleColor: #04090f;
  --formColor: #8a8a8a;
  --secBg: #cacaca;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #fff;
  font-family: "Raleway", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  font-feature-settings: "lnum";
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.container {
  width: 100%;
  max-width: 1256px;
  padding: 0 20px;
  margin: 0 auto;
}

.btn {
  display: inline-flex;
  align-items: center;
  font-weight: bold;
  font-size: 17px;
  line-height: 120%;
  color: var(--titleColor);
}
.btn__arrow {
  display: inline-block;
  margin-left: 14px;
  position: relative;
}
.btn__arrow-svg {
  position: relative;
  z-index: 5;
}
.btn__arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background: var(--accent);
}

.title {
  font-weight: bold;
  font-size: 30px;
  line-height: 115%;
}

.text {
  font-size: 16px;
  line-height: 140%;
}

@media all and (min-width: 1366px) {
  .title {
    font-size: 46px;
  }

  .text {
    font-size: 20px;
  }
}
@media all and (min-width: 1440px) {
  .title {
    font-size: 60px;
    line-height: 120%;
  }

  .text {
    font-size: 23px;
  }
}
.swiper-button-prev, .swiper-button-next {
  width: 56px;
  height: 46px;
}
.swiper-button-prev::after {
  content: url("../images/icons/prev.svg");
}
.swiper-button-next::after {
  content: url("../images/icons/next.svg");
}

.form {
  font-weight: 400;
  color: var(--formColor);
}
.form__inputs {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.form__label {
  width: calc(50% - 10px);
  margin-bottom: 20px;
}
.form__label--textarea {
  margin-bottom: 0;
}
.form__label-title {
  margin-bottom: 5px;
  display: block;
  font-size: 17px;
  line-height: 140%;
}
.form__label input {
  width: 100%;
  max-width: 100%;
  height: 60px;
  padding: 15px;
  border: 1px solid var(--formColor);
  background: transparent;
}
.form__label input::placeholder {
  color: #b6b6b9;
}
.form__download {
  margin-bottom: 32px;
  position: relative;
}
.form__download-label {
  display: flex;
  width: 100%;
  height: 70px;
  background: url("../images/icons/border.svg") no-repeat center/100% 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  cursor: pointer;
}
.form__download-label input {
  display: none;
}
.form__download-title {
  padding-right: 44px;
  position: relative;
}
.form__download-title::after {
  content: "";
  position: absolute;
  right: 0;
  top: -3px;
  width: 24px;
  height: 24px;
  background: url("../images/icons/download.svg") no-repeat center/contain;
}
.form__textarea {
  width: 100%;
  resize: none;
  height: 208px;
  padding: 15px;
  border: 1px solid var(--formColor);
  background: transparent;
}
.form__textarea::placeholder {
  color: #b6b6b9;
}

@media all and (min-width: 1280px) {
  .form__inputs {
    margin-bottom: 10px;
  }
  .form__download {
    margin-bottom: 46px;
  }
}
.header {
  padding-bottom: 68px;
  background: #F5F5F5;
}
.header__top {
  margin-bottom: 12px;
  padding: 14px 0;
  display: flex;
  align-items: center;
}
.header__logo {
  display: inline-block;
  width: 98px;
  position: relative;
  z-index: 5;
}
.header__logo img {
  display: block;
  width: 100%;
}
.header__info {
  padding-bottom: 36px;
  position: relative;
}
.header__info-inner {
  max-width: 520px;
  position: relative;
  z-index: 5;
}
.header__light {
  position: absolute;
  right: 0;
  top: -70px;
}
.header__light-desk {
  display: none;
}
.header__title {
  margin-bottom: 24px;
  color: var(--titleColor);
  font-weight: 800;
  font-size: 35px;
  line-height: 110%;
}
.header__title span {
  font-size: 1.16em;
}
.header__text {
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
}
.header__text p {
  margin-bottom: 16px;
}
.header__text p:last-child {
  margin-bottom: 0;
}
.header__design {
  margin-bottom: 20px;
  height: 284px;
  position: relative;
  background: #cacaca;
}
.header__img {
  width: 100%;
  max-width: 335px;
  max-width: none;
  height: 338px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}
.header__img img {
  display: block;
}

@media all and (min-width: 1280px) {
  .header {
    padding-bottom: 0;
  }
  .header__light {
    right: auto;
    left: 0;
    top: 0;
  }
  .header__light-desk {
    width: 300px;
    display: block;
  }
  .header__main {
    display: flex;
  }
  .header__bottom {
    display: none;
  }
  .header__info {
    max-width: 720px;
    padding-bottom: 80px;
    position: static;
  }
  .header__design {
    width: 40%;
    height: auto;
    flex-grow: 1;
    margin-bottom: 0;
  }
  .header__img {
    width: 100%;
    max-width: 100%;
    height: auto;
    left: -130px;
    transform: none;
  }
  .header__img img {
    width: 70%;
  }
  .header__light-mob {
    display: none;
  }
}
@media all and (min-width: 1366px) {
  .header__top {
    margin-bottom: 50px;
    padding: 0;
  }
  .header__info {
    padding-top: 30px;
  }
  .header__logo {
    width: 150px;
  }
  .header__title {
    margin-bottom: 32px;
    font-size: 60px;
  }
  .header__text {
    font-size: 20px;
  }
  .header__text p {
    margin-bottom: 20px;
  }
  .header__img img {
    width: 100%;
    max-width: 851px;
  }
}
@media all and (min-width: 1440px) {
  .header__main {
    width: 100%;
    max-width: 1570px;
    margin-left: auto;
  }
  .header__top {
    margin-bottom: 110px;
  }
  .header__title {
    margin-bottom: 40px;
    font-size: 80px;
  }
  .header__text {
    font-size: 23px;
  }
  .header__text p {
    margin-bottom: 24px;
  }
  .header__info {
    margin: 0;
    padding-top: 45px;
    padding-bottom: 126px;
  }
  .header__info-inner {
    max-width: 570px;
  }
  .header__design {
    width: 50%;
    max-width: 847px;
  }
  .header__img {
    left: -180px;
  }
}
@media all and (min-width: 1700px) {
  .header__light {
    left: 0;
  }
  .header__light-desk {
    width: 605px;
  }
  .header__design {
    height: auto;
    max-width: 860px;
  }
}
@media all and (min-width: 1921px) {
  .header__main {
    margin: 0 auto;
  }
  .header__info {
    position: relative;
  }
  .header__light {
    left: -300px;
  }
}
.header__circle-one {
  position: absolute;
  left: 0;
  top: -20px;
}
.header__circle-two {
  position: absolute;
  left: -90px;
  bottom: 0;
}
.header__slider {
  background: var(--formColor);
  overflow: hidden;
}
.header__slider-inner {
  padding-left: 50px;
  min-height: 70px;
  display: flex;
  align-items: center;
}
.header__slider-person {
  margin-right: 70px;
  font-weight: 600;
  color: #fff;
  white-space: nowrap;
}
.header__slider-score {
  color: var(--accent);
}
.header--survey {
  position: relative;
}
.header--survey .header__title {
  font-weight: 700;
}
.header--survey .header__text {
  margin-bottom: 48px;
}
.header--survey .header__img {
  width: auto;
  max-width: 1175px;
  height: auto;
}
.header--survey .header__img img {
  max-width: none;
}

@media all and (max-width: 1700px) {
  .header__circle-one {
    left: 300px;
  }
  .header__circle-two {
    display: none;
  }
}
@media all and (max-width: 1365px) {
  .header__circle-one {
    width: 300px;
  }
  .header__slider-inner {
    font-size: 20px;
  }
}
@media all and (max-width: 1279px) {
  .header__circle-one {
    left: auto;
    right: 0;
  }
  .header--survey {
    padding-bottom: 0;
  }
  .header--survey .header__bottom {
    display: none;
  }
  .header--survey .header__design {
    height: auto;
    margin-bottom: 0;
  }
  .header--survey .header__img {
    position: static;
  }
  .header--survey .header__img picture {
    display: flex;
    justify-content: center;
  }
  .header--survey .header__img img {
    width: 50%;
    max-width: 100%;
  }
}
@media all and (max-width: 767px) {
  .header__circle-one {
    width: 240px;
  }
  .header--survey .header__img img {
    width: 80%;
  }
}
@media all and (max-width: 460px) {
  .header__circle-one {
    width: 180px;
  }
  .header__slider-inner {
    font-size: 16px;
  }
}
.main {
  flex-grow: 1;
}

.event {
  padding: 52px 0 64px;
}
.event__title {
  margin-bottom: 40px;
}
.event__list {
  padding: 0;
  margin: 0;
  position: relative;
  list-style-type: none;
  counter-reset: num;
}
.event__list-wrapper {
  position: relative;
}
.event__list-wrapper::before {
  content: "";
  position: absolute;
  left: 20px;
  top: 0;
  bottom: 0;
  width: 3px;
  background: var(--accent);
}
.event__item {
  margin-bottom: 37px;
  padding-left: 62px;
  display: flex;
  position: relative;
}
.event__item:last-child {
  margin-bottom: 0;
}
.event__item::before {
  content: "0" counter(num);
  counter-increment: num;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  border: 3px solid #fff;
  border-radius: 50%;
  background: var(--accent);
  color: var(--titleColor);
  text-align: center;
  font-size: 16px;
  line-height: 19px;
}
.event__item--start::before {
  content: "";
  width: 21px;
  height: 21px;
  left: 11px;
}
.event__time {
  margin-right: 14px;
  font-weight: 600;
}
.event__info {
  padding-right: 2px;
  padding-top: 2px;
}
.event__img {
  display: none;
}

@media all and (min-width: 1280px) {
  .event {
    padding: 0;
  }
  .event__inner {
    width: 100%;
    max-width: 1920px;
    padding-left: 0;
    display: flex;
    position: relative;
    z-index: 5;
  }
  .event__img {
    width: calc(100% - 800px);
    max-width: 797px;
    display: flex;
    align-items: flex-end;
    background: var(--secBg);
  }
  .event__about {
    padding: 40px;
    flex-grow: 1;
    background: #fff;
  }
}
@media all and (min-width: 1366px) {
  .event__list-wrapper::before {
    left: 28px;
  }
  .event__item {
    margin-bottom: 50px;
    padding-left: 80px;
  }
  .event__item::before {
    width: 60px;
    height: 60px;
    font-size: 20px;
  }
  .event__item--start::before {
    width: 20px;
    height: 20px;
    left: 19px;
  }
  .event__time {
    margin-right: 20px;
  }
  .event__about {
    padding: 80px 50px;
  }
  .event__title {
    margin-bottom: 50px;
  }
}
@media all and (min-width: 1440px) {
  .event__about {
    padding: 100px 65px;
  }
  .event__list-wrapper::before {
    top: 15px;
  }
  .event__item {
    margin-bottom: 62px;
    padding-left: 104px;
  }
  .event__item::before {
    font-size: 23px;
  }
  .event__item--start::before {
    top: 10px;
  }
  .event__time {
    margin-right: 25px;
  }
}
.photo {
  padding: 50px 0 70px;
  background: #f5f5f5;
  font-weight: 600;
}
.photo__header {
  margin-bottom: 30px;
}
.photo__title {
  margin-bottom: 20px;
}
.photo__slider {
  margin-bottom: 30px;
  overflow: hidden;
}
.photo__action {
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 23px;
  line-height: 140%;
}
.photo__about {
  font-size: 17px;
}
.photo__descr {
  margin-bottom: 24px;
}
.photo__slider-container {
  padding: 0 20px;
}
.photo__item {
  width: 217px;
  overflow: hidden;
}
.photo__img {
  margin-bottom: 12px;
  position: relative;
}
.photo__img-icon img {
  display: block;
}
.photo__img-like {
  width: 130px;
  height: 130px;
  position: absolute;
  right: -65px;
  top: -65px;
  background: var(--accent);
  font-weight: 500;
  font-size: 17px;
  color: var(--titleColor);
  border-radius: 50%;
}
.photo__img-like__inner {
  position: absolute;
  left: 33px;
  bottom: 14px;
}
.photo__img-num {
  display: block;
